:root{
  --primary-white: #fff; 
  --primary-orange: #f9ab00;
  --primaey-dark: rgba(255,255,255,0);
  --primary-red:#ef4035;
  --primary-hover-red:#f33501;
  


}
.container-box {
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}






body{
  font-family: "Roboto Condensed", sans-serif;
}
/***************************************NAVBAR*************************************/
nav{
  z-index:2;
}
.logo{
  width: 7rem;
}


/*-------MENU LINKS----------*/
nav a.nav-link{
  
  font-weight:500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav >li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand{
  max-width: 17.25em;
  padding: 0!important;
  height: 7rem !important;
}

.navbar-nav >li > a.active,  .navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
   border-top: 0.1875rem solid #f9ab00;
   border-top: 0.1875rem solid var(--primary-orange);
}



/************************************HEADER************************************/

